body {
    font-family: "Helvetica Neue", Arial;
    font-weight: 200;
}

a {
    color: hsl(200, 50%, 50%);
}

a.active {
    color: hsl(20, 50%, 50%);
}

#example {
    position: absolute;
}

.App {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 1000px;
    height: 800px;
}

.Master {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 200px;
    overflow: auto;
    padding: 10px 40px;
}

.Detail {
    position: absolute;
    left: 300px;
    top: 0;
    bottom: 0;
    right: 0;
    border-left: 1px solid #ccc;
    overflow: auto;
    padding: 40px;
}


